<template>
    <v-container>
        <v-simple-table>
            <template v-slot:default>
            <thead>
                <tr>
                <th class="text-left">ФИО</th>
                <th class="text-left">Роль</th>
                <th class="text-left">Статус</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, id) in guestsGetter" :key="id">
                <td>{{ item.last_name }} {{ item.name }} {{ item.second_name }}</td>
                <td>{{ item.status }}</td>
                <td v-if="item.color" style="color: green;"><v-checkbox readonly color="green" v-model="ch1"></v-checkbox></td>
                <td v-else style="color: red;"><v-checkbox readonly color="red" v-model="ch2"></v-checkbox></td>
                </tr>
            </tbody>
            </template>
        </v-simple-table>
        <br>
        <v-card-actions class="justify-center">
            <v-btn 
                :loading="isLoad" 
                @click="getPlace"
                outlined
            >Показать место
            </v-btn>
        </v-card-actions>
        <div v-if="img.length > 0">
            <v-card-title></v-card-title>
            <span v-html="img"></span>
        </div>
    </v-container>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import axios from 'axios'

    export default {
        components: {
        },

        data() {
            return {

                isLoad: false,
                ch1: true,
                ch2: false,
                img: '',
            }
        },

        mounted() {
            if (!this.$store.state.user || Object.keys(this.$store.state.user).length == 0) {
            this.$fire({
                title: "Уведомление",
                text: "Необходимо авторизоваться!",
                type: "error",
            })
            .then(this.$router.push('/howtouse'));
        }

            this.$emit('update:title', 'Участники')
            document.title = 'Технополис "ЭРА" | Участники';
            this.getMyGuests()
        },

        methods: {
            ...mapActions(['getGuests']),
            async getMyGuests() {
                await this.getGuests(this.$route.params.id)
            },

            async getPlace() {
                this.isLoad = true
                await this.$store.dispatch('getUsers')
                await axios.post(this.$store.state.getUrl + 'api/getPlace', {
                    guests: await this.$store.state.users,
                    user: JSON.parse(localStorage.getItem('user')).id
                })
                .then(res => {
                    this.img = res.data
                    this.isLoad = false
                })
            }
        },
        computed: mapGetters(['guestsGetter', 'placeGetter'])
    }
</script>

